* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
@font-face {
    font-family: 'Tribeca';
    font-weight: 900;
    src: local('Tribeca'), url('./fonts/Tribeca.ttf') format('opentype');
}

.font-link {
  font-family: 'Jetbrains Mono';
}
